import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    user: {}
  },
  mutations: {
      SET_USER (state, value) {
          state.user = value
      }
  },
  actions: {
  },
    getters: {
      GET_USER (state) {
          return state.user
      }
    }
})
